<template>
  <div>
    這裡是 後台 產品列表
  </div>
</template>

<script>
import adminAPI from '../../apis/admin';

export default {
  name: 'Products',
  props: ['token'],
  data() {
    return {
      products: [],
      pagination: {},
    };
  },
  methods: {
    async getProducts() {
      const response = await adminAPI.getAllProducts();
      const { data } = response;
      if (response.status !== 200) {
        throw new Error('Get Products Fail');
      }
      this.products = data.data;
      this.pagination = data.meta.pagination;
    },
  },
  created() {
    this.getProducts();
  },
};
</script>
